@import url(https://fonts.googleapis.com/css2?family=Calistoga&family=Orelega+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Calistoga&display=swap);
body {
  margin: 0;
  font-family: 'Calistoga', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f6f6f7;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.title {
  font-size: 2rem;
  margin: 20px auto;
  color: #f6f6f7;
  background-color: #40916c;
  text-shadow: -1px 1px 1px #081c15;
  width: 180px;
  padding: 10px;
}

.footer {
  position: absolute;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 20px auto;
  width: 100%;
  font-family: 'Courier New', Courier, monospace;
  text-decoration: none
}

div::selection {
  background-color: #e63946;
  color: whitesmoke;
}

span::selection {
  background-color: #e63946;
  color: whitesmoke;
}

span::-moz-selection {
  background-color: #e63946;
  color: whitesmoke;
}
.game {
    margin-top: 100px;
}

.number-box {
    width: 800px;
    height: 120px;
    overflow-x: hidden;
    font-size: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 50px auto;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 9px 24px rgb(0 0 0 / 2%), 0 9px 24px rgb(0 0 0 / 2%);
    transition: all 0.3s ease-in-out;
    position: relative;
    cursor: text;
}

.number-box-focused {
    box-shadow: 0 9px 24px rgb(0 0 0 / 8%), 0 9px 24px rgb(0 0 0 / 8%);
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}

.blur {
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
}

.done-numbers {
    -webkit-flex: 1 1;
            flex: 1 1;
    color: #b7e4c7;
    text-align: right;
    overflow-x: hidden;
    display: inline-block;
    height: 20;
    direction: rtl;
    letter-spacing: 5px;
    font-family: 'Colfax',sans-serif
}

.type-numbers {
    -webkit-flex: 1 1;
            flex: 1 1;
    overflow-x: hidden;
    letter-spacing: 5px;
    border-left: 1px solid #40916c;
    font-family: 'Colfax' ,sans-serif;
    padding-left: 10px;
    color: #2b2d42;
    width: 50%;
}

.invisible {
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
}

.meter-container { 
    display: -webkit-flex; 
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: #2b2d42;
}

.meter {
    margin: 20px;
}

.box {
    display: inline-block;
    font-size: 2rem;
}

.center {
    background-color: white;
    height: 100px;
    width: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 25px;
}

.subtitle {
    font-size: .8rem;
    font-family: 'Orelega One', cursive;
}

.hide {
    -webkit-transform: scale(0.8, 0.8);
            transform: scale(0.8, 0.8);
    transition: all 0.3s ease-in-out;
    margin-top: -25px;
    opacity: 0;
}

.show {
    transition: all 0.3s ease-in-out;
    -webkit-transform: scale(1.0, 1.0);
            transform: scale(1.0, 1.0);
    opacity: 100;
}

.timer {
    margin-right: 20px;
    margin-top: -35px;
}

.score-container {
    height: 80%;
    width: 100%;
    position: absolute;
    z-index: 5000;
    margin: 0px auto;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    margin-top: -50px;
}

.score-box {
    background-color: white;
    height: 40%;
    width: 500px;
    margin: auto;
    font-family: 'Colfax' ,sans-serif;
    border-radius: 20px;
    box-shadow: 0 9px 24px rgb(0 0 0 / 12%), 0 9px 24px rgb(0 0 0 / 12%);
    transition: all 0.3s ease-in-out;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position:relative;
}

.big-emoji {
    font-size: 4rem;
    height: 130px;
    width: 100%;
}

.results {
    background-color: #40916c;
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 100%;
    border-radius: 20px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Calistoga', cursive;
    color: whitesmoke;
    text-shadow: -1px 1px 1px #081c15;
    font-size: 3rem;
    /*setting font-size 3rem for the dashes in the middle*/
}

.meter-val {
    font-size: 2rem;
}

.meter-caption {
    font-size: 1rem;
}

.meter-final {
    padding: 0px 10px;
    width: 200px;
}

.exit {
    position: absolute;
    right: 20px;
    top: 20px;
    width:30px;
    height: 30px;
    border-radius: 15px;
    border: none;
    background-color: #40916c;
    color: whitesmoke;
    transition: all 0.2s ease-in-out;
    font-size: 18px;

}

.exit:hover {
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}

.exit:active {
    background-color: #74c69d;
}
